/* Typographic v2.9.3 https://github.com/corysimmons/typographic */
html {
  font-family: 'Geneva', 'Tahoma', 'Verdana', 'sans-serif';
  font-weight: 500;
  color: #000;
  font-size: 16px;
  line-height: 1.75em;
}
@media (min-width: 600px) {
  html {
    font-size: calc( 16px + (20 - 16) * ((100vw - 600px) / (1140 - 600)) );
  }
}
@media (min-width: 1140px) {
  html {
    font-size: 20px;
  }
}
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td {
  margin: 0;
  padding: 0;
}
p,
blockquote,
pre,
address,
dl,
ol,
ul,
table {
  margin-bottom: 1.75em;
}
h1,
